.specialMenu__button {
  margin-top: 15px;
}

.app__specialMenu {
  flex-direction: column;
  background: var(--color-black);
}

.app__specialMenu-title {
  margin-bottom: 2rem;
  text-align: center;
}

.app__specialMenu-menu {
  width: 100%;
  margin: 2rem 0 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
}

.app__specialMenu-menu_heading {
  font-family: var(--font-base);
  font-weight: 600;
  font-size: 45px;
  line-height: 58.5px;
  letter-spacing: 0.04em;
  color: var(--color-white);
}

.app__specialMenu-menu_wine,
.app__specialMenu-menu_cocktails {
  flex: 1;
  width: 100%;
  flex-direction: column;
}
.app__specialMenu-menu_img{
  width: 35%;
  margin: 0 2rem;
}
.app__specialMenu-menu_img img{
  width: 100%;
  height: auto;
}
.app__specialMenu-menu_items{
  display: flex;
  flex-direction: column;
  margin: 2rem 0;
  width: 100%;
}
/**Media Querys**/
