.app__menuItem-name .menuItem__name-p{
  color: #DCCA87;
}
.app__menuItem-sub .menuItem-sub-p{
  color: #aaa;
}

.app__menuItem{
  width: 100%;
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
}

.app__menuItem-head{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app__menuItem-sub{
  width: 100%;
  margin-top: 0.2rem;
}

.app__menuItem-name{
  flex: 1;
}

.app__menuItem-dash {
  width: 90%;
  height: 1px;
  background: var(--color-golden);
  margin: 0 1rem;
}

.app__menuItem-price{
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}